import {goWindow} from '@/api/navigate.js'  // 页面跳转
var _this
export default {
	name:'Header',
	data(){
		return{
			city:""
		}
	},
	created() {
		_this = this
		this.city = this.$store.state.city
	},
	watch:{
		'$store.state.city':(newValue)=>{
			_this.city = newValue;
			//console.log(_this.city)
		}
	},
	methods:{
		handleCity(){
			goWindow('/pages/city/City')
		},
		handleSearch(){
			goWindow('/other_pages/sousuo/suosuo')
		}
	}
	
}
